import React, {Component} from 'react';
import pageHead from "../../../../../packages/ext/pageTitle/singletons/pageTitle";
import container from "../../../../../packages/core/container/singletons/container";
import BlogServiceInterface from "../../../domain/services/BlogServiceInterface";
import BlogTab from "../views/tabs/blogTab";
import breadcrumbFacade from "../../../../../packages/bundles/breadcrumb/domain/facades/breadcrumbFacade";

export default class ArticleListComponent extends Component {

    private title = 'Блог';

    private service: BlogServiceInterface;

    state = {
        collection: null,
    };

    constructor(props) {
        super(props);
        this.service = container.get('resume.services.blog');
    }

    componentDidMount() {
        let collection = this.service.findAll();
        collection.then((collection) => {
            this.setState({collection});
            breadcrumbFacade.add(this.title, '/blog');
        });
    }

    render() {
        let collection = this.state.collection;

        if (collection === null) {
            return null;
        }

        pageHead.setPageTitle(this.title);
        return (
            <>
                {/*<h3 className="fw-bold">
                    {this.title}
                </h3>*/}

                <BlogTab posts={collection}/>
            </>
        );
    }
}
