import ProjectServiceInterface from "../../../domain/services/ProjectServiceInterface";
import BaseCrudRestApiAdapter from "./BaseCrudRestApiAdapter";

export default class ProjectRestApiAdapter extends BaseCrudRestApiAdapter implements ProjectServiceInterface {

    endpointCollection = 'projects';

    async findAll() {
        return this.findAllByCondition(this.endpointCollection, {}, [this.filterCollectionByHidden, this.prepareCollection]);
    }

    private prepareCollection(collection) {
        for (let i in collection) {
            if (collection.hasOwnProperty(i)) {
                let item = collection[i];
                for (let j in item.images) {
                    if (item.images.hasOwnProperty(j)) {
                        let imageEntity = item.images[j];
                        if (!imageEntity.hasOwnProperty('thumbnail')) {
                            imageEntity.thumbnail = imageEntity.original;
                            // imageEntity.originalHeight = '50px';
                        }

                    }
                }
            }
        }
        return collection;
    }
}
