import React from 'react';
import Routes from "../resources/config/routes";
import AppRouter from "../../packages/ext/app/router";
import WebLayout from "../presentation/web/layouts/layout";
import {Route, Switch} from "react-router";
import HomeComponent from "../../module/resume/presentation/react/components/homeComponent";
import FloatContainers from "../presentation/web/components/FloatContainers";
import BeforeRenderWidget from "../../packages/ext/app/widgets/BeforeRenderWidget";

export default function IncloudApp(props) {
    return (
        <>
            <AppRouter>
                <Switch>
                    <Route path="/:name">
                        <WebLayout>
                            {Routes}
                        </WebLayout>
                    </Route>
                    <Route path="/">
                        <BeforeRenderWidget/>
                        <Route exact path="/" component={HomeComponent}/>
                    </Route>
                </Switch>
            </AppRouter>

            <FloatContainers/>
        </>
    );
}
