import BaseRestApiAdapter from "./BaseRestApiAdapter";

export default abstract class BaseCrudRestApiAdapter extends BaseRestApiAdapter {

    protected endpointCollection: string;

    async findAll() {
        return this.findAllByCondition(this.endpointCollection, {}, this.filterCollectionByHidden);
    }

    async findOneById(id: number) {
        return this.findOneByCondition(this.endpointCollection,{id}, this.prepareEntity, this.findAll.bind(this));
    }

    protected prepareEntity(entity) {
        return entity;
    }
}
