import BlogServiceInterface from "../../../domain/services/BlogServiceInterface";
import MarkdownIt from "markdown-it";
import BaseCrudRestApiAdapter from "./BaseCrudRestApiAdapter";
import FileHelper from "../../../../../packages/ext/file/helpers/FileHelper";

export default class BlogRestApiAdapter extends BaseCrudRestApiAdapter implements BlogServiceInterface {

    endpointCollection = 'blog';

    async findOneById(id: number) {
        let entity = await this.findOneByCondition(this.endpointCollection,{id}, this.prepareEntity, this.findAll.bind(this));
        return new Promise((resolve, reject) => {
            if (entity.hasOwnProperty('contentFile')) {
                let uri = entity['contentFile'];
                this.axiosInstance.get(uri)
                    .then((result) => {
                        let ext = FileHelper.getExtension(uri);
                        if(ext === 'md') {
                            let md = new MarkdownIt();
                            entity['contentHtml'] = md.render(result.data);
                        } else if(ext === 'html') {
                            entity['contentHtml'] = result.data;
                        }
                        resolve(entity);
                    })
                    .catch((e) => {
                        reject();
                    })
            } else {
                resolve(entity);
            }
        });
    }

    protected prepareEntity(entity) {
        let md = new MarkdownIt();
        if (entity.hasOwnProperty('contentMd')) {
            entity['contentHtml'] = md.render(entity.contentMd);
        }
        return entity;
    }
}
