import AboutServiceInterface from "../../../domain/services/AboutServiceInterface";
import BaseRestApiAdapter from "./BaseRestApiAdapter";
import {Axios} from "axios";
import ContactServiceInterface from "../../../domain/services/ContactServiceInterface";

export default class AboutRestApiAdapter extends BaseRestApiAdapter implements AboutServiceInterface {

    constructor(protected axiosInstance: Axios, protected contactService: ContactServiceInterface) {
        super(axiosInstance);
    }

    async findOne() {
        return new Promise((resolve, reject) => {
            let uri = this.apiEndpoint('about');
            this.axiosInstance.get(uri)
                .then((result) => {
                    let entity = result.data;
                    this.contactService.findAll()
                        .then((collection) => {
                            entity.contacts = collection;
                            resolve(entity);
                        })
                        .catch((e) => {
                            reject(e);
                        });
                })
                .catch((e) => {
                    reject(e);
                });
        });
    }
}
