import React, {Component} from 'react';
import pageHead from "../../../../../packages/ext/pageTitle/singletons/pageTitle";
import container from "../../../../../packages/core/container/singletons/container";
import BlogServiceInterface from "../../../domain/services/BlogServiceInterface";
import Html from "../../../../../packages/widgets/html/Html";
import breadcrumbFacade from "../../../../../packages/bundles/breadcrumb/domain/facades/breadcrumbFacade";

export default class ArticleDetailComponent extends Component {

    private title = 'Блог';

    private service: BlogServiceInterface;

    state = {
        collection: null,
        entity: null,
    };

    constructor(props) {
        super(props);
        this.service = container.get('resume.services.blog');
    }

    /*componentDidUpdate(prevProps: Readonly<{}>, prevState: Readonly<{}>, snapshot?: any) {
        console.log(prevProps);
    }*/

    componentDidMount() {
        let id = this.props['match']['params']['id'];
        this.service.findOneById(id).then((entity) => {
            this.setState({entity});
            breadcrumbFacade.add(this.title, '/blog');
            breadcrumbFacade.add(entity.title, '/blog/' + entity.id);
        });
    }

    render() {
        let entity = this.state.entity;

        if (entity === null) {
            return null;
            // return <NotFoundPage/>;
        }

        pageHead.setPageTitle(entity.title);
        return (
            <>
                <h3 className="fw-bold">
                    {entity.title}
                </h3>

                <div className="card mt-3">
                    <div className="card-body">
                        <Html>
                            {entity.contentHtml}
                        </Html>
                    </div>
                </div>
            </>
        );
    }
}
