import React from 'react';
import {useLocation} from "react-router";
import eventEmitter from "../../../core/event/singletons/eventEmitter";
import PageTitleEventEnum from "../enums/PageTitleEventEnum";

function beforeRender(location: Location) {
    eventEmitter.emit(PageTitleEventEnum.BEFORE_RENDER, location);
}

export default function BeforeRenderWidget(props) {
    const location = useLocation();
    React.useEffect(() => beforeRender(location), [location]);
    return <></>;
}
