import React from 'react';
import {Route} from 'react-router-dom';
import SkillsComponent from "../../../module/resume/presentation/react/components/skillsComponent";
import AboutComponent from "../../../module/resume/presentation/react/components/aboutComponent";
import ContactsComponent from "../../../module/resume/presentation/react/components/contactsComponent";
import CompanyComponent from "../../../module/resume/presentation/react/components/companyComponent";
import LinksComponent from "../../../module/resume/presentation/react/components/linksComponent";
import ProjectsComponent from "../../../module/resume/presentation/react/components/projectsComponent";
import ArticleListComponent from "../../../module/resume/presentation/react/components/articleListComponent";
import ArticleDetailComponent from "../../../module/resume/presentation/react/components/articleDetailComponent";
import MdReaderRoutes from "../../../packages/bundles/mdReader/web";

export default (
    <>
        <Route exact path="/about" component={AboutComponent}/>
        <Route exact path="/contacts" component={ContactsComponent}/>
        <Route exact path="/companies" component={CompanyComponent}/>
        <Route exact path="/links" component={LinksComponent}/>
        <Route exact path="/projects" component={ProjectsComponent}/>
        <Route exact path="/blog" component={ArticleListComponent}/>
        <Route exact path="/blog/:id" component={ArticleDetailComponent}/>
        <Route exact path="/skills" component={SkillsComponent}/>

        <Route component={MdReaderRoutes}/>
    </>
);
