import {Axios} from "axios";
import CollectionHelper from "../../../../../packages/ext/array/CollectionHelper";
import NotFoundError from "../../../../../packages/core/contract/errors/NotFoundError";

export default abstract class BaseRestApiAdapter {

    constructor(protected axiosInstance: Axios) {

    }

    protected apiEndpoint(name) {
        const basePath = window['appConfig']['restApi']['basePath'];
        return basePath + '/' + name + '.json';
    }

    protected filterCollectionByHidden(collection) {
        CollectionHelper.setDefaultValuesInCollection(collection, {isHidden: false});
        return CollectionHelper.filterCollectionByCondition(collection, {isHidden: false});
    }

    protected async findAllByCondition(endpoint, condition, callbackForResult = null) {
        return new Promise((resolve, reject) => {
            let uri = this.apiEndpoint(endpoint);
            this.axiosInstance.get(uri)
                .then((result) => {
                    let collection = result.data;
                    collection = CollectionHelper.filterCollectionByCondition(collection, condition);
                    if(callbackForResult) {
                        let resultCollection = this.prepareCollectionByCallback(collection, callbackForResult);
                        if(resultCollection !== null && resultCollection !== undefined) {
                            collection = resultCollection;
                        }
                    }
                    resolve(collection);
                })
                .catch((e) => {
                    reject(e);
                });
        });
    }

    protected async prepareCollectionByCallback(collection, callbackForResult) {
        let resultCollection = collection;
        if(typeof callbackForResult === 'function') {
            resultCollection = callbackForResult(resultCollection);
        } else if(typeof callbackForResult === 'object') {
            for(let i in callbackForResult) {
                if(callbackForResult.hasOwnProperty(i)) {
                    let callback = callbackForResult[i];
                    resultCollection = callback(resultCollection);
                }
            }
        }
        return resultCollection;
    }

    protected async findOneByCondition(endpoint, condition, callbackForResult = null, callbackForAll = null) {
        return new Promise((resolve, reject) => {
            callbackForAll = callbackForAll ? callbackForAll : this.findAllByCondition.bind(this, endpoint);
            callbackForAll(condition).then((collection: object) => {
                let newCollection = CollectionHelper.filterCollectionByCondition(collection, condition);
                if(newCollection.hasOwnProperty(0)) {
                    let entity = newCollection[0];
                    // this.prepareEntity(entity);
                    entity = this.prepareCollectionByCallback(entity, callbackForResult);
                    resolve(entity);
                } else {
                    // reject(new Error());
                    // throw new NotFoundError();
                    // resolve(null);
                }
            });
        });
    }

    protected prepareEntity(entity) {

    }
}
