import React from 'react';
import {Breadcrumb} from "react-bootstrap";
import {useSelector} from "react-redux";

function BreadcrumbsWidget(props) {
    const breadcrumbItem = useSelector(store => store['breadcrumbItem']);
    let collection = breadcrumbItem.collection;
    let className = props.className;
    if (collection.length) {
        return (
            <Breadcrumb className={className}>
                {collection.map(function (itemEntity, key) {
                    return (
                        <Breadcrumb.Item key={key} href={"#" + itemEntity.route}>
                            {itemEntity.title}
                        </Breadcrumb.Item>
                    );
                })}
            </Breadcrumb>
        );
    } else {
        return null;
    }
}

export default React.memo(BreadcrumbsWidget);
